@import url('https://fonts.googleapis.com/css2?family=Train+One&display=swap');
:root{
  --redColor: rgb(182, 36, 0);
}
*{
  /*border: 1px solid darkgreen;*/
  padding:0;
  margin:0;
}
.App {
  text-align: center;
  align-items: center;
  flex-wrap:wrap;
  color:white;
  background-color: black;
  width:100vw;
  height:100vh;
  justify-content: center;
  flex-direction:column;
}
.wide-box{
  width: 100%;
  margin: 1vh 0;
  display:flex;
  align-items: center;
  padding-left:30vw;
}
i{
  height:100%;
  margin:0;
}
i.oranger{
  color:var(--redColor);
}
p{
  height:100%;
  margin:0;
  padding-left: 1vw;
  display:flex;
  align-items: center;
}

.header{
  font-family: 'Train One', cursive;
  width: 100%;
  height:30vh;
  display: flex;
  justify-content: center;
  color: var(--redColor);
  font-size:25vh;
  margin-bottom: 2vh;
}
@media (max-aspect-ratio: 1.1/1) and (min-aspect-ratio:1/100){
  .wide-box{
    padding-left:13vw;
  }
}
